import React, { Component } from 'react';
import {
  Button,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  Form,
  FormGroup,
  Input,
  Label,
  Row,
} from 'reactstrap';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css';

class Forms extends Component {
  categoryArr = ["General", "General/EWS", "SC", "ST", "OBC"]
  sexArr = ['Male', 'Female']
  minor=["Yes","No"]
  disable=["Yes","No"]

  constructor(props) {
    super(props);
    this.state = {
      name: '',
      fatherName: '',
      motherName: '',
      address: '',
      dob: new Date(),
      joiningDate: new Date(),
      courseName: '',
      pincode: '',
      phone: '',
      category: null,
      sex: null,
      nationality: '',
      minor:null,
      disable:null,
      adharNumber:'',
      "enrollType":[]
    };

    this.handleChange = this.handleChange.bind(this);
    this.register = this.register.bind(this);
    this.handleChangeDob = this.handleChangeDob.bind(this);
    this.handleChangejDate = this.handleChangejDate.bind(this);
    this.handleChangeCat = this.handleChangeCat.bind(this);
    this.handleChangeSex = this.handleChangeSex.bind(this);
    this.handleChangeMinor = this.handleChangeMinor.bind(this);
    this.handleChangeDisable = this.handleChangeDisable.bind(this);
    this.handleChangeEnroll = this.handleChangeEnroll.bind(this);
  }
  componentDidMount(){
    this.fetchOptions()
}   
 fetchOptions(){
    fetch('https://api.cedu.in/V2/api/getCourse', {
      method: 'post',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
    }).then((res) => {
            return res.json();
        }).then((json) => {
           let values = json;
            console.log("sdafsa",values)
            this.setState({enrollType: values.response})
            console.log("SDfsd",this.state.enrollType);
        });
}
  handleChangeDob = date => {
    this.setState({
      dob: date
    });
  };
  handleChangejDate = date => {
    this.setState({
      joiningDate: date
    });
  }
  handleChangeCat(option){
    this.setState({ category: option.value });
  }
  handleChangeSex(option){
    this.setState({ sex: option.value });
  }
  handleChangeMinor(option){
    this.setState({ minor: option.value });
  }
  handleChangeDisable(option){
    this.setState({ disable: option.value });
  }
  handleChangeEnroll(option) {
    console.log("dsfsd",option.target.value)
    this.setState({ courseName: option.target.value});
      
      // this.setState({ studentInfo: event.value });
}
  handleChange(event, comingName) {
    if (comingName == 'name') {
      this.setState({ name: event.target.value });
    }
    if (comingName == 'phone') {
      this.setState({ phone: event.target.value });
    }
    if (comingName == 'fatherName') {
      this.setState({ fatherName: event.target.value });
    }
    if (comingName == 'motherName') {
      this.setState({ motherName: event.target.value });
    }
    if (comingName == 'address') {
      this.setState({ address: event.target.value });
    }
    if (comingName == 'adhar') {
      this.setState({ adhar: event.target.value });
    }
    if (comingName == 'dob') {
      this.setState({ dob: event.target.value });
    }
    if (comingName == 'pincode') {
      this.setState({ pincode: event.target.value });
    }
    if (comingName == 'nationality') {
      this.setState({ nationality: event.target.value });
    }
  }
  register(event) {
    console.log("event", this.state);
    alert('A name was submitted: ' + this.state.name);
    fetch('https://api.cedu.in/V2/api/addStudent', {
      method: 'post',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        name: this.state.name,
        phone: Number(this.state.phone),
        address: this.state.address,
        fatherName: this.state.fatherName,
        motherName: this.state.motherName,
        dob: this.state.dob,
        category: this.state.category,
        sex: this.state.sex,
        minor:this.state.minor,
        disable:this.state.disable,
        adhar:this.state.adhar,
        joiningDate: this.state.joiningDate,
        courseName: this.state.courseName,
        nationality: this.state.nationality,
        pincode: Number(this.state.pincode)
      })
    }).then((Response) => Response.json())
      .then((Result) => {
        console.log(Result)
        if (Result.response_code == 200) {
          alert(this.state.courseName + "student registered successfully");
          this.props.history.push("/Dashboard");
        }
        else
          alert('Server error !!!!!Please Try Again');
      })
  }
  render() {

    return (
      <div className="animated fadeIn">
        <Row>
          <Col xs="12">
            <Card>
              <CardHeader>
                <i className="fa fa-edit"></i>Student Registration Form
                </CardHeader>
              <CardBody>
                <Form className="form-horizontal">
                  <FormGroup>
                    <Label htmlFor="prependedInput">Student Name</Label>
                    <div className="controls">
                      <Input id="prependedInput" size="16" type="text" onChange={(event) => { this.handleChange(event, 'name') }} />
                    </div>
                  </FormGroup>
                  <Row>
                    <Col xs="12" md="6">
                      <FormGroup>
                        <Label htmlFor="appendedInput">Father Name</Label>
                        <div className="controls">
                          <Input id="appendedInput" size="16" type="text" onChange={(event) => { this.handleChange(event, 'fatherName') }} />
                        </div>
                      </FormGroup>
                    </Col>
                    <Col xs="12" md="6">
                      <FormGroup>
                        <Label htmlFor="appendedInput">Mother Name</Label>
                        <div className="controls">
                          <Input id="appendedInput" size="16" type="text" onChange={(event) => { this.handleChange(event, 'motherName') }} />
                        </div>
                      </FormGroup>
                    </Col>
                  </Row>
                  <Row>
                    <Col xs="12" md="3">
                      <FormGroup>
                        <Label htmlFor="appendedInput">Course Name</Label>
                        <div className="controls">
                        <select className="form-control" onChange={this.handleChangeEnroll}>
          <option>---select---</option>
            {
            this.state.enrollType.map((option, index) => 
            (<option key={index} value={option.name}>{option.name}</option>))
            }
</select>
                        </div>
                      </FormGroup>
                    </Col>
                    <Col xs="12" md="3">
                      <FormGroup>
                        <Label htmlFor="appendedInput">Date Of Birth</Label>
                        <div className="controls">
                          <DatePicker
                            selected={this.state.dob}
                            onChange={this.handleChangeDob}
                            dateFormat='dd/MM/yyyy'
                          />
                        </div>
                      </FormGroup>
                    </Col>
                    <Col xs="12" md="3">
                      <FormGroup>
                        <Label htmlFor="appendedInput">Phone Number</Label>
                        <div className="controls">
                          <Input id="appendedInput" size="16" type="text" onChange={(event) => { this.handleChange(event, 'phone') }} />
                        </div>
                      </FormGroup>
                    </Col>
                    <Col xs="12" md="3">
                      <FormGroup>
                        <Label htmlFor="appendedInput">Pincode</Label>
                        <div className="controls">
                          <Input id="appendedInput" size="16" type="text" onChange={(event) => { this.handleChange(event, 'pincode') }} />
                        </div>
                      </FormGroup>
                    </Col>
                  </Row>
                  <Row>
                    <Col xs="12" md="3">
                      <FormGroup>
                        <Label htmlFor="appendedInput">Joining Date</Label>
                        <div className="controls">
                          <DatePicker
                            selected={this.state.joiningDate}
                            onChange={this.handleChangejDate}
                            dateFormat='dd/MM/yyyy'
                          />
                        </div>
                      </FormGroup>
                    </Col>
                    <Col xs="12" md="3">
                      <FormGroup>
                        <Label htmlFor="appendedInput">Nationality</Label>
                        <div className="controls">
                          <Input id="appendedInput" size="16" type="text" onChange={(event) => { this.handleChange(event, 'nationality') }} />
                        </div>
                      </FormGroup>
                    </Col>
                    <Col xs="12" md="3">
                      <FormGroup>
                        <Label htmlFor="appendedInput">Category</Label>
                        <div className="controls">
                          <Dropdown options={this.categoryArr}  onChange={this.handleChangeCat} value={this.state.category}/>
                        </div>
                      </FormGroup>
                    </Col>
                    <Col xs="12" md="3">
                      <FormGroup>
                        <Label htmlFor="appendedInput">Sex</Label>
                        <div className="controls">
                        <Dropdown options={this.sexArr}  onChange={this.handleChangeSex} value={this.state.sex}/>
                        </div>
                      </FormGroup>
                    </Col>
                  </Row>
                  <Row>
                  <Col xs="12" md="4">
                      <FormGroup>
                        <Label htmlFor="appendedInput">Minor</Label>
                        <div className="controls">
                          <Dropdown options={this.minor}  onChange={this.handleChangeMinor} value={this.state.minor}/>
                        </div>
                      </FormGroup>
                    </Col>
                    <Col xs="12" md="4">
                      <FormGroup>
                        <Label htmlFor="appendedInput">Disable</Label>
                        <div className="controls">
                          <Dropdown options={this.disable}  onChange={this.handleChangeDisable} value={this.state.disable}/>
                        </div>
                      </FormGroup>
                    </Col>
                    <Col xs="12" md="4">
                      <FormGroup>
                        <Label htmlFor="appendedInput">Adhar</Label>
                        <div className="controls">
                          <Input id="appendedInput" size="16" type="text" onChange={(event) => { this.handleChange(event, 'adhar') }} />
                        </div>
                      </FormGroup>
                    </Col>
                  </Row>
                  <FormGroup>
                    <Label htmlFor="appendedInput">Address</Label>
                    <div className="controls">
                      <Input id="appendedInput" size="16" type="textarea" onChange={(event) => { this.handleChange(event, 'address') }} />
                    </div>
                  </FormGroup>
                  <div className="form-actions">
                    <Button type="submit" color="primary" onClick={this.register}>Save changes</Button>
                    <Button color="secondary">Cancel</Button>
                  </div>
                </Form>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    );
  }
}

export default Forms;
